import React , { Component } from 'react'
import Count from './Count/Count'
import Data from './data.js'
import './styles.css'
class About extends Component{
	render(){
		return(
			<div id="about">
				<div className="about-flex container">
					<div className="about-left">
						<img src="https://ik.imagekit.io/hbj42mvqwv/WhatsApp_Image_2021-05-12_at_01.44.10_LcDOXxYKN.jpeg" alt="Kamal Nanda" className="kamal-photo" />
						<h2>Who is this guy ?</h2>
					</div>
					<div className="about-right">
						<h1>I am Kamal Nanda a Full Stack Web Developer based in Delhi, India. I'm persuing BCA from VIPS. I have good experience in working with React.JS and can make modern looking, responsive and fully dynamic websites.</h1>					
						<div className="count-holder">
							{
								Data.map(( data , index) => {
									return(
										<Count key={index} value={data.value} title={data.title} />
									)
								})
							}
						</div>
					</div>
				</div>
			</div>
		)
	}
}
export default About